	export default {
		data() {
			return {
				messages: [{
					title: ''
				}],
				msgs: [{
						title: '第一篇文章',
						msgpic1: '../../static/timg.jpg',
						msgpic2: '../../static/timg.jpg',
						msgpic3: '../../static/timg.jpg',
					},
					{
						title: '第二篇文章',
						// msgpic1:'hasaki'
						msgpic1: '../../static/timg.jpg',
					},
					{
						title: '第三篇文章',
						msgpic1: '../../static/timg.jpg',
						msgpic2: '../../static/timg.jpg',
						msgpic3: '../../static/timg.jpg',
					},
// 					{
// 						title: '第四篇文章',
// 						msgpic1: '../../static/picture-4-1.jpg',
// 						msgpic2: '../../static/picture-4-2.jpg',
// 						msgpic3: '../../static/picture-4-3.jpg',
// 					}
				],
				showall: true,
				showview: false,
				showtravel: false,
				showflowerworld: false,
				searchstr: '',
				result: false
			}
		},
		onLoad() {

		},
		methods: {
			clickshow: function() {
				this.showall = true;
				this.showview = false;
				this.showtravel = false;
				this.showflowerworld = false;
			},
			search: function() {
				this.showall = false;
				this.result = true;
			},
			clickshow1: function() {
				this.showall = false;
				this.showview = true;
				this.showtravel = false;
				this.showflowerworld = false;
			},
			clickshow2: function() {
				this.showall = false;
				this.showview = false;
				this.showtravel = true;
				this.showflowerworld = false;
			},
			clickshow3: function() {
				this.showall = false;
				this.showview = false;
				this.showtravel = false;
				this.showflowerworld = true;
			}
		},
		computed: {
			filterArticles: function() {
				var filterArray = this.msgs;
				var searchString = this.searchstr;
				//若搜索关键字为空则返回原始数据
				if (!searchString) {
					return filterArray;
				} else {
					//去除无用字符转换成小写
					searchString = searchString.trim().toLowerCase();
					//过滤数组内容
					filterArray = filterArray.filter(function(item) {
						//找到了标题含有searchString
						if (item.title.toLowerCase().indexOf(searchString) != -1) {
							return item;
						}
					})
					//最终的过滤数组
					return filterArray;
				}
			}
		}
	}
